.back-login-ui{
    background-color: #fefefe;
    padding: 0.8rem;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.2rem;
    font-size: 14px;
    cursor: pointer;
    color: #165efe;
    font-weight: 500;
    transition: all 0.2s;
}
.back-login-ui:hover{
    background-color: #ededed;
}
.back-login-ui svg{
    font-size: 1.2rem;
}